import {View} from "./View";
import {html} from "lighterhtml";

export class Button extends View{
    action = () => {};
    label = "";

    constructor (label) {
        super();
        this.label = label;
    }

    set_action(action) {
        this.action = action;
        return this;
    }

    render() {
        return html`
            <button onclick=${() => this.action()}>${this.label}</button>
        `;
    }
}

export default function(label) {
    return new Button(label);
}
